.country-search-form {
    overflow: hidden;
}

.form-input {
    width: 100%;
    float: left;
    padding: 15px 20px;
    text-align: center;
    font-family: inherit;
    font-size: 1.1rem;
    letter-spacing: 0.05rem;
    outline: 0;

}

.form-input:focus {
     border-color: gray;
 }

.form-btn {
    background-color: white;
    border: 0.1em solid #FFFFFF;
    font-weight: bold;
    font-family:'Roboto',sans-serif;
    color: black;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}

.form-btn:hover {
    color: white;
    background-color: black;
}
