.list-item img{
    width: 50px;
    height: 30px;
    margin-bottom: 20px;
}

.list {
    width: auto;
}

.list-item {
    list-style-type: none;
    margin-top: 20px;
    border: white 2px solid;
    text-align: center;
    background: ghostwhite;
    color: black;
    opacity: 0.7;
}

.list-item a {
    color: black;
}
.form {
    text-align: center;
    margin-left: 70px;
}

.form-control {
    display: block;
    width: 100%;
    height: 38px;
    font-size: 14px;
    color: #555;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 4px;

}